import { Component                                            } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController  } from 'ionic-angular';
import { Contacts, User                                       } from '../../providers';
import { FormBuilder, FormGroup, Validators                   } from '../../../node_modules/@angular/forms';

@IonicPage()
@Component({
  selector: 'page-addcomment',
  templateUrl: 'addcomment.html',
})

export class AddcommentPage {

  btnAdd      = false;
  spinner     = false;
  data : any  = [] ;
  msg         = '' ;
  public commentForm: FormGroup;

  constructor(private user : User, public formBuilder: FormBuilder, public contact: Contacts, public navCtrl: NavController, public viewCtrl : ViewController, public navParams: NavParams) {
    this.commentForm = formBuilder.group({
      comment: ['', [Validators.required]]
    });

  } 

  addComment(){

    if(this.btnAdd)
      return;

    this.btnAdd   = true;
    this.spinner  = true;
    var $this     = this;
  
    this.data.userid    = this.user.userInfo.userid;
    this.data.folio     = this.contact.contact.visit.visitid;
    this.data.officeid  = this.contact.contact.visit.office;
    this.data.token     = this.user.userInfo.token;
    var response        = this.contact.addComment(this.data);

    Promise.all([response]).then(data => {
      
      this.spinner = false;

      if(data[0]){

        this.msg = 'Comentario agregado con Ã©xito.';
        setTimeout(() => {
          $this.viewCtrl.dismiss();
        }, 2000);
      }else{
        $this.btnAdd = false;
      }
   
    }); 
  }

  cancel(){
    this.viewCtrl.dismiss();
  }

}



